import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';

import { withStyles } from '@material-ui/core/styles';

import api from '../assets/gapi';
import Grabber from '../assets/grabber';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  appBar: {
    
  },
  grow: {
    flexGrow: 1,
  },
  container: {
    padding: 20
  },
  button: {

  }
});

class App extends Component {

  state = {
    links: 'https://reactjs.org',
    variables: [
      {
        name: 'Title',
        pattern: '<title.*>(.+)<\\/title>',
        flags: 'i'
      }
    ],
    buildString: 'Title: %Title%',
    result: '',
    user: api.user,
  };

  constructor (props) {
    super(props);
    api.onUserUpdate = user => this.setState({ user });
  }

  authHandler = () => {
    api.authorize()
      .then(user => this.setState({ user }));
  }

  runHandler = () => {
    let grab = Grabber.grab(this.state.variables, this.state.buildString);
    Promise.all(this.state.links.split('\n').map(link => api.fetch(link)))
      .then(result => result.map(text => grab(text)).join('\n'))
      .then(result => this.setState({ result }));
  }

  changeHandler = name => event => {
    this.setState({ [name]: event.target.value });
  }

  render() {

    const { classes } = this.props,
      { links, variables, buildString, user, result } = this.state;

    return (
      <div className={classes.root}>
        <AppBar position="static" className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.grow}>
              Data grabber
            </Typography>
            <Button className={classes.button} color="inherit" onClick={this.authHandler} >
              { user ? user.w3.ig : 'Login' }
            </Button>
          </Toolbar>
        </AppBar>
        <div className={classes.container}>
          <Grid container spacing={24} className={classes.grid}>
            <Grid item xs={4}>
              <TextField
                label="Links"
                fullWidth
                multiline
                rows={30}
                variant="outlined"
                value={links}
                onChange={this.changeHandler('links')}
              />
            </Grid>
            <Grid item xs={8}>
              {variables.map((variable, index) => (
                <Grid container spacing={24}>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      label="Name"
                      value={variable.name}
                    />
                  </Grid>
                  <Grid item xs={9}>
                    <TextField
                      fullWidth
                      label="Pattern"
                      value={variable.pattern}
                    />
                  </Grid>
                  <Grid item xs={3}>
                    <TextField
                      fullWidth
                      label="Flags"
                      value={variable.flags}
                    />
                  </Grid>
                </Grid>
              ))}
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                fullWidth
                label="Build string"
                value={buildString}
              />
            </Grid>
            <Grid item xs={12}>
              <Button color="primary" className={classes.button} onClick={this.runHandler} >
                Grab
              </Button>
            </Grid>
            { result.length > 0 &&
              <Grid item xs={12}>
                <TextField
                  label="Result"
                  fullWidth
                  multiline
                  rows={result.split('\n').length}
                  variant="outlined"
                  value={result}
                />
              </Grid>
            }
          </Grid>
        </div>
      </div>
    );
  }

}

export default withStyles(styles)(App);
